.folder-structure {
  width: 100%;
  height: 100vh;
  display: flex;
  .sidebar {
    min-width: 15%;
    padding: 12px;
    border-right: 1px solid gray;
    resize: horizontal;
    overflow: scroll;
    .folder {
      cursor: pointer;
      .title {
        font-size: 12px;
        padding-left: 4px;
      }
    }
  }
}
